// @flow
import { useEffect } from 'react';
import { useQuery } from '@apollo/client';
import { LIST_PROPERTY } from 'graphql/queries/properties/list';
import { LIST_PROPERTY_STATUSES } from 'graphql/queries/properties/status';
import _get from 'lodash.get';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import { toastr } from 'react-redux-toastr';
import type { Property as PropertyType } from 'types/Property';

const STEP_LIMIT = 3;

const usePropertiesStatuses = (): {
  properties: Array<PropertyType>,
  steps: string[],
  isLoading: boolean,
} => {
  const { loading, error, data } = useQuery(LIST_PROPERTY);
  const {
    loading: loadingStatuses,
    error: errorStatuses,
    data: statusesData,
  } = useQuery(LIST_PROPERTY_STATUSES);
  const properties = _get(data, 'listProperties', []);
  const statuses = _get(statusesData, 'listPropertyStatuses', []);

  useEffect(() => {
    const { message } = error || {};
    const { message: statusesMessage } = errorStatuses || {};
    if (message || statusesMessage) {
      toastr.error(
        PROPERTY_ERROR_MESSAGE.fetchFailure,
        message,
        statusesMessage,
      );
    }
  }, [error, errorStatuses]);

  return {
    properties,
    steps: statuses.slice(0, STEP_LIMIT).map(status => _get(status, 'Name')),
    isLoading: loading || loadingStatuses,
  };
};

export default usePropertiesStatuses;
