// @flow
import React, { Fragment } from 'react';
import _isEmpty from 'lodash/isEmpty';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import Property from './Property';
import usePropertiesStatuses from './usePropertiesStatuses';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

const PropertyList = (): Element<'div' | typeof Fragment> => {
  const { properties, steps, isLoading } = usePropertiesStatuses();
  const classes = useStyles();

  if (_isEmpty(properties) && !isLoading) {
    return <></>;
  }

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, {
        [classes.loading]: isLoading,
      })}
    >
      {properties.map(property => (
        <Property property={property} steps={steps} key={property.PropertyId} />
      ))}
    </BlockUi>
  );
};

export default PropertyList;
