// @flow
import React, { Fragment } from 'react';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import { Widget } from 'components/Widget';
import Statuses from './Statuses';
import type { Property as PropertyType } from 'types/Property';
import _isEmpty from 'lodash/isEmpty';

const useStyles = makeStyles(theme => ({
  widget: {
    marginBottom: theme.spacing(3),
  },
  description: {
    ...theme.typography.h5,
    marginBottom: theme.spacing(3),
    color: theme.palette.primary.gray,
  },
  stepper: {
    padding: 0,
  },
  stepLabel: {
    fontFamily: theme.typography.fontFamily.avenirLT,

    '& .MuiStepLabel-active': {
      color: theme.palette.primary.main,
    },
  },
}));

type Props = {
  property: PropertyType,
  steps: string[],
};

const Property = ({
  property = {},
  steps = [],
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  if (_isEmpty(property)) return <></>;

  return (
    <Widget
      className={classes.widget}
      key={property.PropertyId}
      title="Home Sale Status"
    >
      <div className={classes.description}>
        Current status of your property{' '}
        <strong>{_get(property, 'PropertyData.FullAddress')}</strong>
      </div>
      <Statuses steps={steps} step={property.StatusId - 1} />
    </Widget>
  );
};

export default Property;
